.eventbox{
  width: 19px;
  height: 19px;
  margin: 1px;
  border: 1px solid #979797;
  border-radius: 2px;
}

.eventbox.past{
  background: #eeffee;
}
.eventbox.future{
  background: white;
}
.eventbox.present{
  background: greenyellow;
  outline : 2px solid green;
}

.eventbox.single-event , .eventbox.birthday{
  outline : 1px solid green;
}
.eventbox.multi-event {
  background-color: #cdedfd;
  background-image: -webkit-linear-gradient(131deg, #cdedfd 0%, #ffec82 37%, #ffcfd2 100%);
  background-image: linear-gradient(319deg, #cdedfd 0%, #ffec82 37%, #ffcfd2 100%);
  outline : 1px solid green;
}
.eventbox:hover {
  z-index: 1;
  outline: 1px solid green;
}
.eventbox.multi{
  background: black
}

/* Tooltip container */
.tooltip {
  position: relative;
}

/* Tooltip text */
.tooltip .tooltiptext {
  visibility: hidden;
  width: 120px;
  background-color: black;
  color: #fff;
  text-align: center;
  font: normal;
  padding: 5px 0;
  border-radius: 6px;

  /* Position the tooltip text */
  position: absolute;
  z-index: 1;
  bottom: 125%;
  left: 50%;
  margin-left: -60px;

  /* font settings */
  font-family: monospace;
  font-size: medium;


  /* animation */
  opacity: 0;
  -webkit-transition: opacity 0.5s;
  transition: opacity 0.5s;
}

/* Tooltip arrow */
.tooltip .tooltiptext::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 50%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: black transparent transparent transparent;
}

/* Show the tooltip text when you mouse over the tooltip container */
.tooltip:hover .tooltiptext {
  visibility: visible;
  opacity: 1;
}
.grid-box{
